import uniIcons from "../uni-icons/uni-icons.vue";
export default {
	name: "UniSearchBar",
	components: {
		uniIcons
	},
	props: {
		placeholder: {
			type: String,
			default: "请输入搜索内容"
		},
		radius: {
			type: [Number, String],
			default: 5
		},
		clearButton: {
			type: String,
			default: "auto"
		},
		cancelButton: {
			type: String,
			default: "auto"
		},
		cancelText: {
			type: String,
			default: '取消'
		},
		bgColor: {
			type: String,
			default: "#F8F8F8"
		},
		maxlength: {
			type: [Number, String],
			default: 100
		}
	},
	data() {
		return {
			show: false,
			showSync: false,
			searchVal: ""
		}
	},
	watch: {
		searchVal() {
			this.$emit("input", {
				value: this.searchVal
			})
		}
	},
	methods: {
		searchClick() {
			if (this.show) {
				return
			}
			this.searchVal = ""
			this.show = true;
			this.$nextTick(() => {
				this.showSync = true;
			})
		},
		clear() {
			this.searchVal = ""
		},
		cancel() {
			this.$emit("cancel", {
				value: this.searchVal
			});
			this.searchVal = ""
			this.show = false
			this.showSync = false

			uni.hideKeyboard()




		},
		confirm() {

			uni.hideKeyboard();




			this.$emit("confirm", {
				value: this.searchVal
			})
		}
	}
};
