import api from '@/services/api.js';
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import mSearch from '@/components/mehaotian-search/mehaotian-search.vue';
export default {
	components: {
		mSearch,
	},
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			columnList: [],
			searchName: '',
			// val0: '',
		};
	},
	// onLoad() {
	// 	this.loadData();
	// },
	onShow() {
		this.loadData();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			this.carouselList = await this.$api.config.getBanners();
			this.columnList = await this.$api.config.getCols();

			this.swiperLength = this.carouselList.length;
			// this.carouselList = carouselList;
			// let carouselList = await this.$api.config.getCols();
			// let goodsList = await this.$api.json('goodsList');
			// this.goodsList = goodsList || [];
		},
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
		},
		navToArticle(data) {
			let str = encodeURIComponent(JSON.stringify(data));
			uni.navigateTo({
				url: `/pages/index/article?article=${str}`
			})
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			// let id = item.title;
			console.log(item);
			let product = encodeURIComponent(JSON.stringify(item));
			// let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?product=${product}`
			})
		},
		changeMyCarts(item){
			this.$api.account.editMyCart(item, 1).then(data=>{
				this.$api.msg('添加成功');
			});
		},
		search(e, val) {
			// let search = JSON.stringify(searchName);
			this['val'+val] = e;
			uni.navigateTo({
				url: '/pages/search/search?search=' + e
			})
		}
	},

























}
